#contact {
    padding: 6rem 3rem;
    /* height: calc(450px - 60px); */
    background-color: #909FA3;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    /* padding: 5rem 0 0 5rem; */
  }
  
  .contact {
    font-family: 'Inter';
    font-weight: 400;
    font-size: 36pt;
    color: rgb(255, 255, 255);
    margin: 0;
    padding-left: 2rem;
  }
  
  .email-button {
    display: inline-block;
    margin-top: 2rem;
    padding: 0.75rem 5rem;
    background-color: #494945;
    color: white;
    font-family: 'Inter';
    font-weight: 300;
    font-size: 16px;
    border: none;
    border-radius: 6px;
    text-decoration: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-left: 2rem;
  }
  
  .email-button:hover {
    background-color: #2b2b2b;
  }
  

  /* For tablets */
@media (max-width: 768px) {
    .contact {
      font-size: 28pt; /* Reduce font size */
    }
  
    .email-button {
      padding: 0.75rem 4rem; /* Reduce button padding */
    }
  }
  
  /* For mobile phones */
  @media (max-width: 480px) {
    #contact {
      padding: 5rem 1.5rem; 
      align-items: center;
      justify-content: center;
      text-align: center;
    }
  
    .contact {
      font-size: 22pt; /* Further reduce font size */
      padding-left: 0;
    }
  
    .email-button {
      width: 50%; /* Make button full-width */
      padding: 0.75rem 2rem;
      margin: 1rem auto 0 auto;
      box-sizing: border-box; /* Include padding in the width calculation */
      text-align: center;
    }
  }