#about {
    min-height: calc(100vh - 60px);
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    padding: 5rem 0 0 4rem;
    box-sizing: border-box;
  }
  
  .about {
    font-family: 'Inter';
    font-weight: 300;
    font-size: 28pt;
    margin: 0;
    margin-left: 2rem;
  }
  
  .about-content {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    /* padding-left: 2rem; */
    box-sizing: border-box;
    /* padding-top: 5rem; */
    padding: 5vh 3vw; 
    gap: 4vw;
    flex-wrap: wrap;
  }
  
  .about-image {
    max-width: 500px;
    width: 30vw;
    height: auto;
    border-radius: 10px;
  }
  
  .about-text {
    /* display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    box-sizing: border-box;
    padding-left: 5rem;
    padding-top: 0rem; */
    padding-left: 2vw; /* scales with width */
    width: 50vw;
    max-width: 600px;
    font-size: 1.2rem;
  }
  
  .text-one {
    font-family: 'Inter';
    font-size: 13pt;
  }
  
  .text-two {
    font-family: 'Inter';
    font-weight: 300;
    line-height: 1.4;
    font-size: 13pt;
    color: #656565;
    /* width: 50vw; */
  }
  
  .resume-button {
    display: inline-block;
    margin-top: 1rem;
    padding: 0.75rem 5rem;
    background-color: #656565;
    color: white;
    font-family: 'Inter';
    font-weight: 300;
    font-size: 16px;
    border: none;
    border-radius: 6px;
    text-decoration: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .resume-button:hover {
    background-color: #494945;
  }


/* For tablets and smaller devices */
@media (max-width: 992px) {
    #about {
      padding: 3rem 2rem; /* Adjust padding */
      align-items: center; /* Center content */
    }
  
    .about {
      margin-left: 0;
      text-align: center;
    }
  
    .about-content {
      flex-direction: column; /* Stack content vertically */
      align-items: center; /* Center items */
      gap: 2rem;
      padding: 2rem 0;
    }
    
    .about-image {
      width: 60vw; /* Make image larger on tablets */
      max-width: 400px;
    }
  
    .about-text {
      width: 90%; /* Use percentage for better control */
      max-width: 550px;
      padding: 0;
      text-align: center;
    }
  
    .text-two {
      text-align: left; /* Keep body text left-aligned */
      padding: 1rem;
    }
  }
  
  /* For mobile phones */
  @media (max-width: 480px) {
    #about {
      padding: 2rem 1rem; /* Further reduce padding */
    }
  
    .about-image {
      width: 80vw; /* Make image take up more space */
    }
  
    .about-text {
      font-size: 1rem; /* Adjust font size */
      width: 100%; /* Ensure text container takes full width */
    }
  
    .resume-button {
      padding: 0.75rem 2rem;
      width: 80%; /* Make button full-width */
      box-sizing: border-box; /* Include padding in width */
    }
  }

