#home {
    min-height: calc(100vh - 60px);
    height: auto;
    background-color: #ffffff;
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    padding: 20vh 0rem 0 4rem;
    gap: 1rem;
    border-bottom-left-radius: 100px;
    border-bottom-right-radius: 100px;
  }

  .home-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  
  .flower {
    width: 15vw;
    padding: 1rem;
  }
  
  .hello {
    font-family: 'Inter';
    font-weight: 300;
    font-size: 32pt;
    margin: 0;
    margin-left: 2rem;
  }
  
  .intro {
    font-family: 'Inter';
    font-weight: 300;
    font-size: 13pt;
    color: #656565;
    margin-top: 2rem;
    margin-left: 2rem;
    max-width: 70vw;
    line-height: 1.8;
  }

  @media (max-width: 992px) {
    #home {
      padding: 3rem 2rem; /* Adjust padding */
      display: flex;
      flex-direction: column;
      align-items: center; /* Center content */
    }

    .home-content {
      flex-direction: column;
      align-items: center;
      gap: 2rem;
      padding: 2rem 0;
    }

    .hello {
      margin-left: 0;
      text-align: center;
    }
  
    .intro {
      width: 90%; /* Use percentage for better control */
      /* max-width: 550px; */
      text-align: center;
    }
  }
  
  /* For mobile phones */
  @media (max-width: 480px) {
    #home {
      padding: 2rem 1rem;
    }

    .home-content {
      flex-direction: column;
      align-items: center;
      /* gap: 1.5rem; */
    }
  
    .flower {
      width: 60vw; /* Make image take up more space */
    }

    .hello {
      margin-left: 0;
      text-align: center;
    }
  
    .intro {
      font-size: 1rem; /* Adjust font size */
      width: 100%; /* Ensure text container takes full width */
      text-align: left; /* Keep body text left-aligned */
      padding: 0;
      margin: 0 auto;
      /* padding: 1rem; */
    }
  
  }